
export default ({
	namespaced: true,
	state: {
		listScroll:false,
		listScrollMore:false,
		agree: {},
		sns:{},
		menu:[],
		settingActive:false,
		memberNoticeList:[],
		mNoticeActive:false,
	},
	getters: {
		getListScroll: state => {
			return state.listScroll;
		},
		getListScrollMore: state => {
			return state.listScrollMore;
		},
		getAgree: state => {
			return state.agree;
		},
		getMenu: state => {
			return state.menu;
		},
		getSns: state => {
			return state.sns;
		},
		getSettingActive:state => {
			return state.settingActive;
		},
		getMemberNoticeList:state => {
			return state.memberNoticeList;
		},
		getMNoticeActive:state => {
			return state.mNoticeActive;
		}

	},
	mutations: {
		setMenu(state,menu) {
			state.menu = menu;
		},
		setAgree(state,agree) {
			state.agree = agree;
		},
		setSns(state,sns) {
			state.sns = sns;
		},
		setSettingActive(state,bool) {
			state.settingActive = bool;
		},
		setListScroll(state,bool) {
			state.listScroll = bool;
		},
		setListScrollMore(state,bool) {
			state.listScrollMore = bool;
		},
		setMemberNoticeList(state,list) {
			state.memberNoticeList = list;
		},
		setMNoticeActive(state,bool) {
			state.mNoticeActive = bool;
			if(bool==true) {
				setTimeout(() => {
					state.mNoticeActive = false;
				}, 800);
			}
		}

	},
	actions: {
		setSns({commit},sns) {
			commit('setSns',sns)
		},
		setAgree({commit},agree) {
			commit('setAgree',agree)
		},
		setMenu({commit},menu) {
			commit('setMenu',menu)
		},
		setSettingActive({commit},bool) {
			commit('setSettingActive',bool)
		},

		setListScroll({commit},bool) {
			commit('setListScroll',bool)
		},
		setListScrollMore({commit},bool) {
			commit('setListScrollMore',bool)
		},
		setMemberNoticeList({commit},list) {
			commit('setMemberNoticeList',list)
		},
		setMNoticeActive({commit},bool) {
			commit('setMNoticeActive',bool)
		}

    }

})
