import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    /**
     * @ 사용자
     */
    {
        path: "/",
        component: () => import("../views/WEB/index"),
        redirect: `/main`,
        children: [
            {
                path: "testJsonRpc/:id/:uid",
                name: "testJsonRpc",
                component: () => import("../views/WEB/liveStream/testJsonRpc"),
            },
            {
                path: "testUserJsonRpc/:id/:uid/:hostUid",
                name: "testUserJsonRpc",
                component: () => import("../views/WEB/liveStream/testUserJsonRpc"),
            },
            {
                path: "main",
                name: "siteMain",
                component: () => import("../views/WEB/main/main"),
            },
            {
                path: "memberLogin",
                name: "memberLogin",
                component: () => import("../views/WEB/member/login"),
            },

            {
                path: "memberFindPass",
                name: "memberFindPass",
                component: () => import("../views/WEB/member/findPass"),
            },
            {
                path: "JoinIntro",
                name: "memberJoinIntro",
                component: () => import("../views/WEB/member/joinIntro"),
            },
            {
                path: "memberJoin",
                name: "memberJoin",
                component: () => import("../views/WEB/member/join"),
            },
            {
                path: "member/agree/:code",
                name: "memberAgree",
                component: () => import("../views/WEB/member/agree"),
            },
            {
                path: "emailAuth/:code",
                name: "memberEmailAuth",
                component: () => import("../views/WEB/member/emailAuth"),
            },
            {
                path: "memberPassword/:code",
                name: "memberEmailMemberPassword",
                component: () => import("../views/WEB/member/emailModifyPassword"),
            },
            {
                path: "search/:keyword",
                name: "siteSearch",
                component: () => import("../views/WEB/main/search"),
            },
            {
                path: "joinAgree",
                name: "joinAgree",
                component: () => import("../views/WEB/member/joinAgree"),
            },
            {
                path: "mypage",
                component: () => import("../views/WEB/mypage/index"),
                redirect: `mypage/memberModify`,
                children: [
                    {
                        path: "main",
                        name: "mypageMain",
                        component: () => import("../views/WEB/mypage/memberMain"),
                    },
                    {
                        path: "memberModify",
                        name: "mypageMemberModify",
                        component: () => import("../views/WEB/mypage/memberModify"),
                    },
                    {
                        path: "memberPassword",
                        name: "mypageMemberPassword",
                        component: () => import("../views/WEB/mypage/memberPassword"),
                    },
                ]
            },
            {
                path: "video",
                component: () => import("../views/WEB/video/index"),
                redirect: `video/step`,
                children: [
                    {
                        path: "step",
                        name: "videoStep",
                        component: () => import("../views/WEB/video/step/step"),
                    },
                    {
                        path: "setting",
                        component: () => import("../views/WEB/video/setting/index"),
                        redirect: `setting/main`,
                        children: [
                            {
                                path: "main",
                                name: "videoSettingMain",
                                component: () => import("../views/WEB/video/setting/info/main"),
                            },
                            {
                                path: "env",
                                name: "videoSettingEnv",
                                component: () => import("../views/WEB/video/setting/info/liveSetting")
                            },
                            {
                                path: "examSet",
                                name: "videoSettingExamSet",
                                component: () => import("../views/WEB/video/setting/exam/examSettingNormal")
                            },
                            {
                                path: "examDirect",
                                name: "videoSettingExamDirect",
                                component: () => import("../views/WEB/video/setting/exam/examSettingDirect")
                            },
                            {
                                path: "examUpload",
                                name: "videoSettingExamUpload",
                                component: () => import("../views/WEB/video/setting/exam/examSettingExcel")
                            },
                            {
                                path: "dataList",
                                component: () => import("../views/WEB/video/setting/data/index"),
                                redirect: `dataList/all`,
                                children: [
                                    {
                                        path: "all",
                                        name: "videoSettingDataAll",
                                        component: () => import("../views/WEB/video/setting/data/dataList")
                                    },
                                    {
                                        path: "examModify/:no",
                                        name: "videoSettingDataModify",
                                        component: () => import("../views/WEB/video/setting/exam/examModify")
                                    },
                                    {
                                        path: "fileView/:no",
                                        name: "videoSettingDataFileView",
                                        component: () => import("../views/WEB/video/setting/data/fileView")
                                    },
                                ]
                            },
                            {
                                path: "pollList",
                                name: "videoSettingPollList",
                                component: () => import("../views/WEB/video/setting/poll/pollMake")
                            },
                            {
                                path: "voteList",
                                name: "videoSettingVoteList",
                                component: () => import("../views/WEB/video/setting/vote/voteMake")
                            },
                            {
                                path: "boardSetting",
                                name: "videoSettingBoardSetting",
                                component: () => import("../views/WEB/video/setting/board/boardSetting")
                            },
                            ////////// after //////////////
                            {
                                path: "afterExam",
                                name: "videoSettingAfterExam",
                                component: () => import("../views/WEB/video/setting/exam/afterExam")
                            },
                            {
                                path: "examReport/:examNo",
                                name: "videoSettingExamReport",
                                component: () => import("../views/WEB/video/setting/exam/examReport")
                            },
                            {
                                path: "examReportView/:pno/:replyNo",
                                name: "videoSettingExamReportView",
                                component: () => import("../views/WEB/video/setting/exam/examReportView")
                            },
                            {
                                path: "afterQuiz",
                                name: "videoSettingAfterQuiz",
                                component: () => import("../views/WEB/video/setting/quiz/afterQuiz")
                            },
                            {
                                path: "afterQuizView/:pno",
                                name: "videoSettingAfterQuizView",
                                component: () => import("../views/WEB/video/setting/quiz/afterQuizView")
                            },
                            {
                                path: "afterChat",
                                name: "videoSettingAfterChat",
                                component: () => import("../views/WEB/video/setting/chat/afterchat")
                            },
                            {
                                path: "afterPoll",
                                name: "videoSettingAfterPoll",
                                component: () => import("../views/WEB/video/setting/poll/afterPoll")
                            },
                            {
                                path: "afterPollUser/:id",
                                name: "videoSettingAfterPollUser",
                                component: () => import("../views/WEB/video/setting/poll/afterPollUser")
                            },
                            {
                                path: "afterPollUserReport/:replyId",
                                name: "videoSettingAfterPollUserReport",
                                component: () => import("../views/WEB/video/setting/poll/afterPollUserReport")
                            },
                            {
                                path: "afterVote",
                                name: "videoSettingAfterVote",
                                component: () => import("../views/WEB/video/setting/vote/afterVote")
                            },
                            {
                                path: "afterBoard",
                                name: "videoSettingAfterBoard",
                                component: () => import("../views/WEB/video/setting/board/afterBoard")
                            },
                            {
                                path: "afterUser",
                                name: "videoSettingAfterUser",
                                component: () => import("../views/WEB/video/setting/attendUser/afterUser")
                            },
                            {
                                path: "afterData",
                                name: "videoSettingAfterData",
                                component: () => import("../views/WEB/video/setting/data/afterData")
                            }

                        ]
                    },
                ]
            },
            {
                path: "liveStream",
                component: () => import("../views/WEB/liveStream/index"),
                redirect: `liveStream/hostLive`,
                children: [
                    {
                        path: "zoomEnd",
                        name: "liveStreamZoomEnd",
                        component: () => import("../views/WEB/liveStream/zoomEnd"),
                    },
                    {
                        path: "hostLive/:id",
                        name: "liveStreamHostLive",
                        component: () => import("../views/WEB/liveStream/hostLive/hostVideo"),
                    },
                    {
                        path: "hostVideoPc/:id",
                        name: "liveStreamHostLivePc",
                        component: () => import("../views/WEB/liveStream/hostLive/pc/hostVideoPc"),
                    },
                    {
                        path: "hostVideoMobile/:id",
                        name: "liveStreamHostLiveMobile",
                        component: () => import("../views/WEB/liveStream/hostLive/mobile/hostVideoMobile"),
                    },
                    {
                        path: "userLive/intro/:id",
                        name: "liveStreamUserLiveIntro",
                        component: () => import("../views/WEB/liveStream/userLive/userVideoIntro"),
                    },
                    {
                        path: "userLive/:id",
                        name: "liveStreamUserLive",
                        component: () => import("../views/WEB/liveStream/userLive/userVideo"),
                    },
                    {
                        path: "userVideoPc/:id",
                        name: "liveStreamUserLivePc",
                        component: () => import("../views/WEB/liveStream/userLive/pc/userVideoPc"),
                    },
                    {
                        path: "userVideoMobile/:id",
                        name: "liveStreamUserLiveMobile",
                        component: () => import("../views/WEB/liveStream/userLive/mobile/userVideoMobile"),
                    },


                ]
            },
            {
                path: "board/articleList/:mainCode/:subCode/:bid",
                name: "boardArticleList",
                component: () => import("../views/WEB/board/articleList"),
            },
            {
                path: "board/articleRegist/:mainCode/:subCode/:bid/:id",
                name: "boardArticleRegist",
                component: () => import("../views/WEB/board/articleRegist"),
            },
            {
                path: "board/articleView/:mainCode/:subCode/:id",
                name: "boardArticleView",
                component: () => import("../views/WEB/board/articleView"),
            },
            {
                path: "content/:mainCode/:subCode/:cid",
                name: "contentView",
                component: () => import("../views/WEB/content/index"),
            },

        ]
    },
    /**
     * @ 관리자
     */
    {
        path: "/admin",
        component: () => import("../views/ADMIN/index"),
        name: "adminIndex",
        redirect: `/admin/main`,
        children: [
            {
                path: "main",
                name: "adminMain",
                component: () => import("../views/ADMIN/main/main"),
            },
            {
                path: "adminUpdate",
                name: "adminUpdate",
                component: () => import("../views/ADMIN/main/adminUpdate"),
            },
            {
                path: "member",
                component: () => import("../views/ADMIN/member/index"),
                redirect: `/admin/member/memberList`,
                children: [
                    {
                        path: "memberList/:role",
                        name: "adminMemberMemberList",
                        component: () => import("../views/ADMIN/member/memberList"),
                    },
                ]
            },
            {
                path: "setting",
                component: () => import("../views/ADMIN/setting/index"),
                redirect: `/admin/setting/companyInfo`,
                children: [
                    {
                        path: "memberset",
                        name: "adminSettingMemberset",
                        component: () => import("../views/ADMIN/setting/memberset"),
                    },
                    {
                        path: "memberAgree",
                        name: "adminSettingMemberAgree",
                        component: () => import("../views/ADMIN/setting/memberAgree"),
                    },
                    {
                        path: "emailset",
                        name: "adminSettingEmailset",
                        component: () => import("../views/ADMIN/setting/emailSetting"),
                    },
                    {
                        path: "menuSetting",
                        name: "adminSettingMenuSetting",
                        component: () => import("../views/ADMIN/setting/menuSetting"),
                    },
                ]
            },
            {
                path: "run",
                component: () => import("../views/ADMIN/run/index"),
                redirect: `/admin/run/boardList`,
                children: [
                    {
                        path: "boardList",
                        name: "adminRunBoardList",
                        component: () => import("../views/ADMIN/run/board/boardList"),
                    },
                    {
                        path: "keywordList",
                        name: "adminRunKeywordList",
                        component: () => import("../views/ADMIN/run/keywordList"),
                    },
                    {
                        path: "articleList",
                        name: "adminRunArticleList",
                        component: () => import("../views/ADMIN/run/articleList"),
                    },
                    {
                        path: "boardArticleRegist/:id",
                        name: "adminRunBoardArticleRegist",
                        component: () => import("../views/ADMIN/run/board/boardArticleRegist"),
                    },


                ]
            },
            {
                path: "statistics",
                component: () => import("../views/ADMIN/statistics/index"),
                redirect: `/admin/statistics/contentStatistics`,
                children: [
                    {
                        path: "contentStatistics",
                        name: "adminStatisticsContent",
                        component: () => import("../views/ADMIN/statistics/contentStatistics"),
                    },
                    {
                        path: "downStatistics",
                        name: "adminStatisticsDown",
                        component: () => import("../views/ADMIN/statistics/downStatistics"),
                    },
                    {
                        path: "memberStatistics",
                        name: "adminStatisticsMember",
                        component: () => import("../views/ADMIN/statistics/memberStatistics"),
                    },

                ]
            },

        ]
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior(to, from, savedPosition) {

        return {x: 0, y: 0}
    },
    routes
})

export default router
