
export default ({
	namespaced: true,
	state: {
		gno:sessionStorage.getItem('broadcastId'),
		rpcJson:{},
		rpcJsonActive:false,
		broadcastInfo:{},
		broadcastInfoActive:false,
	},
	getters: {
		getGno: state => {
			return state.gno;
		},
		getRpcJson: state => {
			return state.rpcJson;
		},
		getRpcJsonActive: state => {
			return state.rpcJsonActive;
		},
		getBroadcastInfo:state => {
			return state.broadcastInfo;
		},
		getBroadcastInfoActive:state => {
			return state.broadcastInfoActive;
		},

	},
	mutations: {
		setGno(state,gno) {
			state.gno = gno;
		},
		setRpcJson(state,params) {
			state.rpcJson = params;
		},
		setRpcJsonActive(state,bool) {
			state.rpcJsonActive = bool;
		},
		setBroadcastInfo(state,info) {
			state.broadcastInfo = info;
		},
		setBroadcastInfoActive(state,bool) {
			state.broadcastInfoActive = bool;
		},
	},
	actions: {
		setGno({commit},gno) {
			sessionStorage.setItem('broadcastId',gno)
			commit('setGno',gno)
		},
		setRpcJson({commit},params) {
			commit('setRpcJson',params)
		},
		setRpcJsonActive({commit},bool) {
			commit('setRpcJsonActive',bool)
		},
		setBroadcastInfo({commit},params) {
			commit('setBroadcastInfo',params)
		},
		setBroadcastInfoActive({commit},bool) {
			commit('setBroadcastInfoActive',bool)
		},
    }

})
