<template>
    <div>
        <v-overlay :value="overlay" :opacity="opacity" style="z-index:100000000;">

            <v-progress-circular
                    :size="50"
                    color="lime accent-1"
                    indeterminate
            ></v-progress-circular>
        </v-overlay>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                overlay: false,
                opacity: '0.4'
            }
        },
        created() {
            this.$eventBus.$on('overlay', this.overlayToggle);
        },
        methods: {
            overlayToggle(cmd) {
                switch (cmd) {
                    case  'hide':
                        this.overlay = false;
                        break;
                    case 'white':
                        this.overlay = true;
                        this.opacity = '0.1';
                        break;
                    default:
                        this.overlay = true;
                        break;
                }
            },
        }
    }
</script>
